import React, { Component } from 'react';
import * as action from 'action';
import { connect } from 'react-redux';
import './POI.css';
import * as THREE from 'three';
//import { MTLLoader, OBJLoader } from 'three-obj-mtl-loader';
import OrbitControls from 'three-orbitcontrols';
import GLTFLoader from 'three-gltf-loader';
import $ from 'jquery';
import Indicator from 'component/UI/Indicator';

class POI extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
       this.props.setHistory(this.props.history);
    }

    this.state = {
    }
    this.startTime = -1;
    this.isStart = false;
  }
  
  getWidth() {
    if (!this.width) {
      this.width = $('#dv-poi').width();
    }
    //console.log(this.width);
    return this.width;
  }

  getHeight() {
    if (!this.height) {
      this.height = $('#dv-poi').height();
    }
    return this.height;
  }

  /*componentDidMount(){
    window.initTabs('#dv-poi ul.tabs', {
      swipeable : true,
      responsiveThreshold : '100vw'
    });
  }*/
  componentDidMount() {
    $("#dv-poi #labels").css('visibility', 'hidden');
    //$("#dv-poi #dv-indicator").css('visibility', 'visible');
    const width = this.mount.clientWidth;
    const height = this.mount.clientHeight;

    this.scene = new THREE.Scene();
    //Add Renderer
    this.renderer = new THREE.WebGLRenderer({ alpha: true });
    //this.renderer.setClearColor("#263238");
    this.renderer.setSize(width, height);
    this.mount.appendChild(this.renderer.domElement);

    const fov = 75;
    const aspect =  width / height;  // the canvas default
    const near = 0.1;
    const far = 100;
    this.camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
    this.camera.position.set(0, 10, 20);
    //add Camera
    /*this.camera = new THREE.PerspectiveCamera(75, width / height, 0.1, 1000);
    this.camera.position.z = 8;
    this.camera.position.y = 15;*/
    //Camera Controls
    const controls = new OrbitControls(this.camera, this.renderer.domElement);
    
    const boxWidth = 1;
    const boxHeight = 1;
    const boxDepth = 1;
    const geometry = new THREE.BoxGeometry(boxWidth, boxHeight, boxDepth);

    {
      /*
      const planeSize = 40;
      const loader = new THREE.TextureLoader();
      const texture = loader.load('https://threejsfundamentals.org/threejs/resources/images/checker.png');
      texture.wrapS = THREE.RepeatWrapping;
      texture.wrapT = THREE.RepeatWrapping;
      texture.magFilter = THREE.NearestFilter;
      const repeats = planeSize / 2;
      texture.repeat.set(repeats, repeats);
  
      const planeGeo = new THREE.PlaneGeometry(planeSize, planeSize);
      const planeMat = new THREE.MeshPhongMaterial({
        map: texture,
        side: THREE.DoubleSide,
      });

      const mesh = new THREE.Mesh(planeGeo, planeMat);
      mesh.rotation.x = Math.PI * -.5;
      this.scene.add(mesh);
*/
      const y = 200;
      this.cubes = [
        makeInstance(this.scene, geometry, 0x44aa88,  0, y, 0, '빨강 빌딩'),
        makeInstance(this.scene, geometry, 0x8844aa, -1000, y, -1000, '파랑 지붕'),
        makeInstance(this.scene, geometry, 0xaa8844,  -1000, y, 0, '파랑 창문'),
        makeInstance(this.scene, geometry, 0x8844aa, 0, y, -1000, '오렌지 빌딩'),
      ];
    }

    {
      const skyColor = 0xB1E1FF;  // light blue
      const groundColor = 0xB97A20;  // brownish orange
      const intensity = 1;
      const light = new THREE.HemisphereLight(skyColor, groundColor, intensity);
      this.scene.add(light);
    }
  
    {
      const color = 0xFFFFFF;
      const intensity = 1;
      const light = new THREE.DirectionalLight(color, intensity);
      light.position.set(5, 10, 2);
      this.scene.add(light);
      this.scene.add(light.target);
    }
    
    function frameArea(sizeToFitOnScreen, boxSize, boxCenter, camera) {
      const halfSizeToFitOnScreen = sizeToFitOnScreen * 0.5;
      const halfFovY = THREE.MathUtils.degToRad(camera.fov * 0.5);
      const distance = halfSizeToFitOnScreen / Math.tan(halfFovY);
      // compute a unit vector that points in the direction the camera is now
      // in the xz plane from the center of the box
      const direction = (new THREE.Vector3())
          .subVectors(camera.position, boxCenter)
          .multiply(new THREE.Vector3(1, 0, 1))
          .normalize();

      // move the camera to a position distance units way from the center
      // in whatever direction the camera was from the center already
      camera.position.copy(direction.multiplyScalar(distance).add(boxCenter));
      camera.position.y = 1500;
      // pick some near and far values for the frustum that
      // will contain the box.
      camera.near = boxSize / 100;
      camera.far = boxSize * 100;

      camera.updateProjectionMatrix();

      // point the camera to look at the center of the box
      camera.lookAt(boxCenter.x, boxCenter.y, boxCenter.z);
    }
    
    /*********** */ 

    function makeInstance(scene, geometry, color, x, y, z, name) {
      const labelContainerElem = document.querySelector('#labels');
      const material = new THREE.MeshPhongMaterial({color});

      const cube = new THREE.Mesh(geometry, material);
      scene.add(cube);

      cube.position.x = x;
      cube.position.y = y;
      cube.position.z = z;

      const elem = document.createElement('div');
      elem.textContent = name;
      labelContainerElem.appendChild(elem);    
      return {cube, elem};
    }
    
    /*********** */
    {
      const gltfLoader = new GLTFLoader();
      gltfLoader.load('https://threejsfundamentals.org/threejs/resources/models/cartoon_lowpoly_small_city_free_pack/scene.gltf', (gltf) => {
        const root = gltf.scene;
        this.scene.add(root);
  
        // compute the box that contains all the stuff
        // from root and below
        const box = new THREE.Box3().setFromObject(root);
  
        const boxSize = box.getSize(new THREE.Vector3()).length();
        const boxCenter = box.getCenter(new THREE.Vector3());
  
        // set the camera to frame the box
        frameArea(boxSize * 1, boxSize, boxCenter, this.camera);
  
        // update the Trackball controls to handle the new size
        controls.maxDistance = boxSize * 10;
        controls.target.copy(boxCenter);
        controls.update();

        $("#dv-poi #labels").insertAfter("#dv-poi canvas");
      });
    }
    //ADD Your 3D Models here
    this.renderScene();
    //start animation
    this.start();
  }

  start = () => {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate);}
  };

  stop = () => {
    cancelAnimationFrame(this.frameId);
  };

  resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  }

  animate = (time) => {
    //Animate Models Here
    //ReDraw Scene with Camera and Scene Object
    if (this.startTime < 0) {
      this.startTime = time;
    }

    if (time - this.startTime > 1000) {
      if (!this.isStart) {
        this.isStart = true;
        $("#dv-poi #labels").css('visibility', 'visible');
        $("#dv-poi #dv-indicator").attr('class', 'hide');
      }
      this.renderScene(time);
    }
    this.frameId = window.requestAnimationFrame(this.animate);
  };

  renderScene = (time) => {
    if (this.renderer) {
      time *= 0.001;
      if (this.resizeRendererToDisplaySize(this.renderer)) {
        const canvas = this.renderer.domElement;
        this.camera.aspect = canvas.clientWidth / canvas.clientHeight;
        this.camera.updateProjectionMatrix();
      }

      const tempV = new THREE.Vector3();

      this.cubes.forEach((cubeInfo, ndx) => {
        const {cube, elem} = cubeInfo;
        const speed = 1 + ndx * .1;
        const rot = time * speed;
        cube.rotation.x = rot;
        cube.rotation.y = rot;

        // get the position of the center of the cube
        cube.updateWorldMatrix(true, false);
        cube.getWorldPosition(tempV);

        // get the normalized screen coordinate of that position
        // x and y will be in the -1 to +1 range with x = -1 being
        // on the left and y = -1 being on the bottom
        tempV.project(this.camera);

        // convert the normalized position to CSS coordinates
        var x = (tempV.x *  .5 + .5) * this.getWidth();
        var y = (tempV.y * -.5 + .5) * this.getHeight();
        x = x.toFixed(2);
        y = y.toFixed(2);

        // move the elem to that position
        elem.style.transform = `translate(-50%, -50%) translate(${x}px,${y}px)`;
      });

      this.renderer.render(this.scene, this.camera);
    }
  };

  onClick(id) {
    //window.selectTab(id);
  }
/*
  render(){
    return (
      <div id='dv-poi'>
        <canvas id='#three-canvas' />
      </div>);
  }*/
  render() {
    return (
      <div id='dv-poi' ref={mount => { this.mount = mount}} className='container'>
        <Indicator />
        <div id='labels'> </div>
      </div>)
  }
}

const mapStateToProps = (state) => { return {}; }

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(POI);